@import './vars';

/** Light */
@font-face {
  font-family: "New York";
  src: url("./assets/fonts/NewYorkExtraLarge-Semibold.otf"); }

/** Light */
@font-face {
  font-family: "San Francisco";
  font-weight: light;
  src: url("./assets/fonts/SF-Pro-Display-Light.otf"); }

/** Semibold */
@font-face {
  font-family: "San Francisco";
  font-weight: bold;
  src: url("./assets/fonts/SF-Pro-Display-Semibold.otf"); }

* {
  color: $black;
  font-family: 'San Francisco'; }

html, body {
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
  background: $background; }

::-webkit-scrollbar {
  display: none; }

body {
  overscroll-behavior-y: none; }

h1 {
  margin: 0;
  font-family: "New York"; }

h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "San Francisco"; }

.App {
  width: 100%;
  overflow-y: hidden;
  .scroll {
    overflow: hidden;
    width: 100%;
    position: relative; } }

.section-title {
  font-size: 4rem;
  line-height: 100%; }

.section-trailing {
  font-size: 2rem;
  font-weight: bold;
  color: $red;
  line-height: 90%;
  margin-left: 20px; }

.section-subtitle {
  font-size: 1.5rem;
  color: $red;
  font-weight: bold;
  line-height: 100%; }

.flex {
  display: flex;
  align-items: center; }

.flex-end {
  justify-content: flex-end; }
.flex-reverse {
  flex-direction: row-reverse; }
.overflow-hidden {
  overflow: hidden; }

.smooth-scroll {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh; }

@media only screen and (max-width: 900px) {
  .section-title {
    font-size: 2rem; }
  .section-trailing {
    font-size: 1.5rem; }
  .section-subtitle {
    font-size: 1.2rem; } }
