@import '../../vars';

#contact {
  min-height: 100vh;
  padding: 25vh $mobilePadding 0 $mobilePadding;
  user-select: none;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  box-sizing: border-box;
  .container {
    max-width: 800px;
    margin: 0 auto; }
  p.description {
    font-size: 2rem; }
  a {
    button {
      background: $red;
      border: none;
      border-radius: 5px;
      -webkit-box-shadow: $shadow;
      -moz-box-shadow: $shadow;
      box-shadow: $shadow;
      padding: 15px 30px;
      color: $background;
      font-size: 1rem;
      cursor: pointer;
      transition: 0.2s all ease-in-out;
      &:hover {
        -webkit-box-shadow: $shadowHover;
        -moz-box-shadow: $shadowHover;
        box-shadow: $shadowHover; } } } }

.text-align-center {
  text-align: center; }

@media only screen and (max-width: 900px) {
  #contact {
    padding: 100px $mobilePadding 0 $mobilePadding;
    p.description {
      font-size: 1.5rem; } } }

@media only screen and (max-width: 350px) {
  #contact {
    padding: 50px $mobilePadding 0 $mobilePadding; } }
